@import url("https://fonts.googleapis.com/css?family=Pacifico&display=swap");
.highlight-section {
}
.highlight {
  width: 300px;
  margin: 12px;
  border: 1px dashed gainsboro;
  position: relative;
}
.hl-details {
}
.hl-title {
  font-size: 14px;
  font-weight: 600;
  position: absolute;
  top: 2%;
  color: white;
  background: rgba(0, 0, 0);
}
.hl-description {
  font-size: 14px;
}
.hl-meta {
  font-size: 12px;
}
.hl-details > p {
  margin: 4px 2px 20px 10px !important;
}
.header-search {
  padding-top: 20px;
  position: absolute;
  bottom: 15%;
  width: 100%;
}
.header-content {
  position: relative;
}
.search-bar {
  font-size: 20px;
  height: 50px;
  opacity: 0.1;
  transition: opacity 0.3s;
}
.search-bar:focus {
  opacity: 1;
}

.post-link,
.news-link {
  color: rgb(2, 68, 80) !important;
}
.post-link:hover,
.news-link:hover {
  color: rgb(5, 90, 146) !important;
}
/* Auth button navbar */
.auth-btn {
  margin-right: 12px;
}
.nav-auth {
  border-radius: 100px;
}

/* Side Navigation */
.sidenav {
  height: 100%;
  width: 3%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #333333;
}

.sidenav div {
  text-decoration: none;
  color: #ffffff;
  display: block;
  transition: 0.3s;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 18px;
  }
}
.route-nav-item {
  border-bottom: 1px solid white;
  padding: 20px 5px;
  /* padding-bottom: 30px; */
}
.route-nav-item:hover {
  cursor: pointer;
  background: #444444;
}
.route-nav-item:hover ~ .greeting-hover {
  display: block;
}
.icon-route {
  padding: 10px;
}
/* Dropdown */

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  top: 0;
  right: 100%;
  display: none !important;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-radius: 10px;
  margin-top: 10px;
  border: 1px solid gainsboro;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.route-nav-item:hover .dropdown-content {
  display: block !important;
}

.dropdown:hover .dropbtn {
}
.dp-item {
  border-bottom: 0.5px solid gainsboro;
}
.post-preview {
  margin: 10px;
}
.title-section .cat-title,
.title-section .cat-subtitle {
  font-weight: 400;
  text-align: center;
}
.cat-title,
.bold-title {
  font-family: "Pacifico", cursive;
}
.title-section {
  margin-bottom: 30px;
}
.post-interaction,
.post-meta {
  margin: 10px !important;
}
.post-interaction {
  font-size: 16px;
}
.search-icon {
  position: absolute;
  font-size: 19px;
  top: 20%;
  right: 3%;
  color: #003387;
}

.create-post-btn {
  background-color: green !important;
  font-weight: 600 !important;
  font-size: 15px !important;
}
.hl-cat-subtitle {
  margin-top: 20px;
  text-align: center;
  font-weight: 400;
}
/* upload button */
.upload-btn {
  text-align: center;
}
.upload {
  padding: 12px !important;
  border-radius: 20px !important;
  font-size: 20px !important;
  font-weight: 600 !important;
  margin-bottom: 20px;
}
/* Status message */
.status-message {
  margin: 10px;
  text-align: center;
  font-weight: 600;
}
/* Pending loader */
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ellipsis div {
  position: absolute;
  top: 27px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #004085;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 6px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 6px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 26px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 45px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(19px, 0);
  }
}

/* Profile */
.profile-avatar {
  width: 100px;
  border-radius: 50px;
  border: 1px solid gainsboro;
}
.post-submit {
