@keyframes lds-ripple {
  0% {
    top: 90px;
    left: 90px;
    width: 0;
    height: 0;
    opacity: 1;
  }

  100% {
    top: 15px;
    left: 15px;
    width: 150px;
    height: 150px;
    opacity: 0;
  }
}

@-webkit-keyframes lds-ripple {
  0% {
    top: 90px;
    left: 90px;
    width: 0;
    height: 0;
    opacity: 1;
  }

  100% {
    top: 15px;
    left: 15px;
    width: 150px;
    height: 150px;
    opacity: 0;
  }
}

.lds-ripple {
  position: relative;
}

.lds-ripple div {
  box-sizing: content-box;
  position: absolute;
  border-width: 10px;
  border-style: solid;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.lds-ripple div:nth-child(1) {
  border-color: #36173b;
}

.lds-ripple div:nth-child(2) {
  border-color: #7f9626;
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}

.lds-ripple {
  width: 123px !important;
  height: 123px !important;
  -webkit-transform: translate(-61.5px, -61.5px) scale(0.615)
    translate(61.5px, 61.5px);
  transform: translate(-61.5px, -61.5px) scale(0.615) translate(61.5px, 61.5px);
}

.config {
  width: 100%;
  height: 100%;
  left: 40%;
}
